<script>
import lottie from "lottie-web";
import AnimateOnScroll from "../components/AnimateOnScroll.vue";
import MatterBg from "./components/MatterBg.vue";
import animationData from "@/assets/a1.json";
import gsap from "gsap";
import ScrollTrigger from "gsap/ScrollTrigger";
import AOS from "aos";
import "aos/dist/aos.css";
import SwiperMiddle from "./components/SwiperMiddle.vue";
import cubebg from "./components/cubebg.vue";
import GsapBox from "./components/GsapBox.vue";
import ThreeEarth from "./components/ThreeEarth.vue";
import MyLoading from "../components/MyLoading.vue";

gsap.registerPlugin(ScrollTrigger);
export default {
  data() {
    return {
      myloading: true,
      isMobile: window.innerWidth <= 1024,
      middle_key: 0,
      scroll_box_key: 0,
    };
  },
  components: {
    AnimateOnScroll,
    lottie,
    MatterBg,
    SwiperMiddle,
    cubebg,
    GsapBox,
    ThreeEarth,
    MyLoading,
  },
  methods: {
    initblackContainer() {
      gsap.set("#blackContainer", {
        scale: 0.9,
      });
      gsap.to("#blackContainer", {
        scale: 1,
        borderTopLeftRadius: "50px",
        borderTopRightRadius:"50px",
        scrollTrigger: {
          trigger: "#blackContainer",
          start: "top bottom",
          end: "top center",
          scrub: true,
          ease:"none"
        },
      });
    },
    initMyloading() {
      setTimeout(() => {
        this.myloading = false;
      }, 2000);
    },
    scrollbox() {
      this.scroll_box = gsap.context(() => {
        gsap.to("#scroll_box", {
          scrollTrigger: {
            trigger: "#matter_bg", // 
            start: "top center", // 
            end: "top top", // 
            scrub: true, // 
            invalidateOnRefresh: true,
            // markers: true,
            onUpdate: (self) => {
              const progress = self.progress; //  (0  1)
              const scaleValue = gsap.utils.mapRange(0, 1, 1.0, 0.9, progress); //  1.0  0.9
              const borderRadius = gsap.utils.mapRange(0, 1, 0, 50, progress); //  16px  36px
              gsap.set("#scroll_box", {
                scale: scaleValue,
                borderRadius: `${borderRadius}px`, //  px 
              });
            },
          },
        });
      });
    },
    loadAnimation() {
      lottie.loadAnimation({
        container: this.$refs.lottieContainer, // 
        renderer: "svg", // svg, canvas, html
        loop: true, // 
        autoplay: true, // 
        animationData,
      });
    },
    broundscroll() {
      const bround = this.$refs.headbround;
      gsap.set(bround, {
        width: "310px",
        height: "310px",
      });

      gsap.to(bround, {
        width: "1700px",
        height: "1700px",
        scrollTrigger: {
          trigger: this.$refs.headbround, // 
          start: "top bottom", // 
          end: "bottom center", // 
          scrub: true, // 
        },
      });
    },
    svgscroll() {
      const path = this.$refs.svgPath;
      const pathLength = path.getTotalLength();

      // 
      gsap.set(path, {
        strokeDasharray: pathLength,
        strokeDashoffset: pathLength,
      });

      //  ScrollTrigger 
      gsap.to(path, {
        strokeDashoffset: 0,
        ease: "none",
        scrollTrigger: {
          trigger: this.$refs.svgPath,
          start: "top center",
          end: "bottom top",
          scrub: 1,
          invalidateOnRefresh: true,
          // speed: 10, // 
        },
      });
    },
    handleResize() {
      // this.scroll_box?.kill();

      // this.scroll_box = null;
      // this.scrollbox();
      if (window.innerWidth <= 1024) {
        this.isMobile = true;
      } else {
        this.isMobile = false;
      }
      console.log("IsMobile", this.isMobile);
      // this.$bus.$emit("scrollbox", this.isMobile);
    },
  },
  watch: {
    isMobile() {
      this.$nextTick(() => {
        console.log("refersh");
        ScrollTrigger.refresh();
      });
    },
  },
  mounted() {
    this.initMyloading();
    AOS.init();
    this.loadAnimation();
    this.svgscroll();
    this.broundscroll();
    this.scrollbox();
    this.handleResize();
    this.initblackContainer();
    window.addEventListener("resize", this.handleResize);
  },
  beforeDestroy() {
    window.removeEventListener("resize", this.handleResize);
  },
};
</script>



<style scoped>
.arrow {
  position: fixed;
  bottom: 0;
  left: 50%;
  margin-left: -20px;
  width: 40px;
  height: 40px;
  background-image: url(data:image/svg+xml;base64,);
  background-size: contain;
}

.bounce {
  animation: bounce 2s infinite;
}

@keyframes bounce {
  0%,
  20%,
  50%,
  80%,
  100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-30px);
  }
  60% {
    transform: translateY(-15px);
  }
}
.pin-spacer {
  padding: 0;
  position: relative;
}
</style>