<script>
import ProjectHeader from "./components/ProjectHeader.vue";
import tocbot from "tocbot";
import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
export default {
  components: {
    ProjectHeader,
  },
  methods: {
    initTocbot() {
      tocbot.init({
        tocSelector: ".toc", // 
        contentSelector: ".content", // 
        headingSelector: "h1, h2", // 
        collapseDepth: 2, // 
        scrollSmooth: true, // 
      });
    },
    initToc() {
      this.tocbox = gsap.to(this.$refs.tocBox, {
        scrollTrigger: {
          trigger: this.$refs.tocFar,
          start: "top top",
          end: "bottom bottom",
          // scrub: 1,
          markers: true,
          repeatRefresh: true,
        },
        y: () => {
          const tocHeight = this.$refs.tocBox.offsetHeight;
          const containerHeight = this.$refs.tocFar.offsetHeight;
          const scrollDistance = containerHeight - tocHeight;
          return scrollDistance > 0 ? scrollDistance : 0;
        },
      });
    },
  },
  mounted() {
    this.initTocbot();
    // this.initToc();
  },
};
</script>

<style>
.toc {
  overflow-y: auto;
}
.toc > .toc-list {
  overflow: hidden;
  position: relative;
}
.toc > .toc-list li {
  list-style: none;
}
.js-toc {
  overflow-y: hidden;
}
.toc-list {
  margin: 0;
  padding-left: 10px;
}
a.toc-link {
  color: currentColor;
  height: 100%;
}
.is-collapsible {
  max-height: 1000px;
  overflow: hidden;
  transition: all 300ms ease-in-out;
}
.is-collapsed {
  max-height: 0;
}
.is-position-fixed {
  position: fixed !important;
  top: 0;
}
.is-active-link {
  font-weight: 700;
}
.toc-link::before {
  background-color: #eee;
  content: " ";
  display: inline-block;
  height: inherit;
  left: 0;
  margin-top: -1px;
  position: absolute;
  width: 2px;
}
.is-active-link::before {
  background-color: #54bc4b;
}

.content img {
  margin: 0 auto;
  max-width: 500px;
  border-radius: 5%;
  --tw-shadow: 0 25px 50px -12px rgb(0 0 0 / 0.25);
  --tw-shadow-colored: 0 25px 50px -12px var(--tw-shadow-color);
  box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000),
    var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
  margin-top: 4rem;
  margin-bottom: 4rem;
}
.content h1 {
  font-size: 3rem /* 48px */;
  line-height: 1;
  letter-spacing: 0.05em;
  font-weight: 800;
  text-align: center;
  position: relative;
  margin: 2rem 0;
}
.content h1 span {
  position: relative;
}
.content h1 span::after {
  content: "";
  left: 50%;
  transform: translateX(-50%);
  display: block;
  width: 110%;
  height: 2px;
  background-color: #000;
  position: absolute;
  bottom: -5px;
}
.content h2 span {
  position: relative;
  font-size: 2rem;
  font-weight: 800;
  padding-left: 1rem;
  margin: 1rem 0;
}
.content h2 span::before {
  content: "";
  display: block;
  position: absolute;
  width: 3.5px;
  top: 50%;
  transform: translateY(-50%);
  background-color: #000;
  height: 2rem;
}
.content h3 {
  font-size: 1.3rem;
  text-indent: 2rem;
  letter-spacing: 2px;
}
</style>