<template>
    <div
      ref="animatedElement"
      :class="{ 'animate__animated': isVisible, [animationClass]: isVisible }"
      :style="initialStyle"
    >
      <slot></slot>
    </div>
  </template>
  
  <script>
  export default {
    props: {
      animationClass: {
        type: String,
        default: 'animate__bounceInUp'
      },
      initialStyle: {
        type: Object,
        default: () => ({
          opacity: 0, // 
        })
      },
      once: {
        type: Boolean,
        default: true //  true 
      }
    },
    data() {
      return {
        isVisible: false,
      };
    },
    mounted() {
      const observer = new IntersectionObserver((entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            this.isVisible = true;
            if (this.once) {
              observer.unobserve(entry.target); // 
            }
          } else if (!this.once) {
            this.isVisible = false; // 
          }
        });
      });
  
      observer.observe(this.$refs.animatedElement);
    },
  };
  </script>
  
  <style scoped>
  /*  */
  </style>
  