<script>
import Swiper from "swiper/bundle";
import "swiper/swiper-bundle.css";
import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import { throttle } from "lodash";
export default {
  methods: {
    initSwiper() {
      const swiper = new Swiper(".swiper-container", {
        slidesPerView: 1.5,
        spaceBetween: 20,
        centeredSlides: true,
        loop: true,
        parallax: true,
        effect: "slide",
        navigation: {
          nextEl: ".swiper-button-next",
          prevEl: ".swiper-button-prev",
        },
      });
    },
    initScrollbox() {
      let scrollDistance = 0; // 

      //  ScrollTrigger  GSAP 
      gsap.to(".swiper-container", {
        x: 0, // 
        scrollTrigger: {
          trigger: ".swiper-container",
          start: "top center",
          end: "bottom center",
          scrub: true,
          invalidateOnRefresh: true,
          markers: true,
          onUpdate: (self) => {
            const progress = self.progress;
            scrollDistance = gsap.utils.mapRange(0, 1, 0, 100, progress);
            gsap.set(".swiper-container", {
              x: scrollDistance,
              duration: 0.5,
              ease: "power1.out",
            });
          },
        },
      });
    },
  },
  mounted() {
    this.initSwiper();
    this.initScrollbox();
  },
  beforeDestroy() {},
};
</script>

<style scoped>
.swiper-button-prev,
.swiper-button-next {
  position: relative;
  margin: 0 5rem;
  transition: 0.2s cubic-bezier(0.1, -0.6, 0.2, 0);;
}
.swiper-button-prev::before,
.swiper-button-prev::after,
.swiper-button-next::before,
.swiper-button-next::after {
  content: "";
}
.swiper-button-prev:hover,
.swiper-button-next:hover {
  transform: scale(1.2);
}
</style>

