<template>
  <div
    class="flex w-screen min-h-screen rounded-b-2xl scrollbox"
    style="background-color: #6d28a1"
    id="scroll_box"
  >
    <div
      class="flex justify-around w-full min-h-screen scrollbox_container max-lg:flex-col rounded-b-2xl"
      style="background-color: #6d28a1; flex-shrink: 0"
      ref="scrollbox_container"
    >
      <div
        class="flex flex-shrink-0 w-screen h-full bg-transparent px-52 scrollbox_container_card max-lg:px-10 max-lg:h-[450px]"
      >
        <div
          class="m-auto text-3xl font-bold text-center text-white max-lg:text-lg"
        >
          20194%2.7%
        </div>
      </div>
      <div
        class="flex flex-shrink-0 w-screen h-full bg-transparent px-52 scrollbox_container_card max-lg:px-10 max-lg:h-[450px]"
      >
        <div
          class="m-auto text-3xl font-bold text-center text-white max-lg:text-lg"
        >
          L-
        </div>
      </div>
      <div
        class="flex flex-shrink-0 w-screen h-full bg-transparent px-52 scrollbox_container_card max-lg:px-10 max-lg:h-[450px]"
      >
        <div
          class="m-auto text-3xl font-bold text-center text-white max-lg:text-lg"
        >
          712
        </div>
      </div>
      <div
        class="flex flex-shrink-0 w-screen h-full bg-transparent px-52 scrollbox_container_card max-lg:px-10 max-lg:h-[450px]"
      >
        <div
          class="m-auto text-3xl font-bold text-center text-white max-lg:text-lg"
        >
          
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";

gsap.registerPlugin(ScrollTrigger);
export default {
  name: "ScrollBox",
  props: ["isMobile"],
  methods: {
    scrollbox() {
      if (this.isMobile) {
        return;
      }
      // console.log("isMobile", this.isMobile);

      console.log("gsap");
      this.scrollboxAnimation = gsap.context(() => {
        gsap.to(".scrollbox_container", {
          x: () =>
            -(this.$refs.scrollbox_container?.scrollWidth - window.innerWidth) +
            "px",
          ease: "none",
          scrollTrigger: {
            trigger: ".scrollbox",
            pin: true,
            scrub: 1,
            end: () => "+=" + this.$refs.scrollbox_container?.scrollWidth,
          },
        });
      });
    },
  },
  mounted() {
    if (!this.isMobile) {
      this.scrollbox();
    }
    // window.addEventListener("resize", () => {
    //   console.log("resize");
    //   if (this.isMobile) {
    //     this.scrollboxAnimation?.revert();
    //   }
    // });
    // this.$bus.$on("scrollbox", (isMobile) => {
    //   if (isMobile) {
    //     console.log("revert()", isMobile);
    //     this.scrollboxAnimation?.revert();
    //     // this.scrollboxAnimation?.kill();
    //     this.scrollboxAnimation = null;
    //   } else {
    //     console.log("scrollbox", isMobile);
    //     this.scrollbox();
    //   }
    // });
  },
  watch: {
    isMobile: {
      immediate: true,
      handler(newvalue, oldvalue) {
        if (!newvalue && oldvalue) {
          // window.refresh();
          console.log("pc");
          this.scrollbox();
          ScrollTrigger.refresh();
          window.location.reload();
        } else if(newvalue &&!oldvalue) {
          console.log("");
          this.scrollboxAnimation?.revert();
          this.scrollboxAnimation = null;
        }
      },
    },
  },
  beforeDestroy() {
    this.$bus.$off("scrollbox");
  },
};
</script>

